import React from 'react';
// Styled Components
import { ProgressContainer, ProgressBar, ProgressTrough, ProgressLabel } from '../../styled';

const Progress = (props) => {
  return (
    <ProgressContainer>
      <ProgressTrough>
        <ProgressBar progress={(props.progress / props.total) * 100}></ProgressBar>
      </ProgressTrough>
      <ProgressLabel>{props.children}</ProgressLabel>
    </ProgressContainer>
  )
}

export default Progress;



