// 'Link' navigation element for react-router
import { Link } from 'react-router-dom';
// Styled Components
import styled from 'styled-components';

const $dark         = '#442b48';
const $light        = '#f6e8ea';
const $blue         = '#424a7f';
const $blueLight    = '#5661a8';
const $blueDark     = '#2a3053';
const $red          = '#d33f49';
const $redDark      = '#ac323a';
const $accent       = '#5dd9c1';
const $accentDark   = '#41ab96';
const $accentDarker = '#348e7c';
const $accentLight  = '#80fbe3';

export const MainContainer = styled.main`
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #fff;
`;

export const MainNavContainer = styled.nav`
  display: block;
  height: 40%;
  max0width: 960px;
`;

export const BlurbContainer = styled.section`
  align-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: 0 .2rem .8rem .4rem rgba(0,0,0,.2);
  color: ${$dark};
  display: flex;
  flex-direction: column;
  justifty-content: center;
  flex-grow: 2;
  height: 40%;
  margin: 2rem auto 1rem;
  max-width: 960px;
  // min-width: 480px;
  padding: 1rem;
  width: 90%;
`;

export const HeaderContainer = styled.header`
  display: block;
  padding: 1rem;
  text-align: center;
  width: 100%;
`;

export const Title = styled.h1`
  font-family: 'Righteous', 'slab-serif', sans;
  font-size: 3.6rem;
  line-height: 3rem;
  margin: 1rem auto;
  max-width: 18rem;
  text-align: center;
  text-shadow: 0 .6rem .2rem rgba(0,0,0,.2);

  @media only screen and (min-width: 498px) {
    max-width: 20rem;
    font-size: 3.8rem;
    line-height: 3.1rem;
  }

  @media only screen and (min-width: 560px) {
    max-width: 22rem;
    font-size: 4.0rem;
    line-height: 3.6rem;
  }

  @media only screen and (min-width: 720px) {
    max-width: 24rem;
    font-size: 4.2rem;
    line-height: 3.8rem;
  }

  @media only screen and (min-width: 980px) {
    font-size: 4.6rem;
    line-height: 4.2rem;
  }
`;

export const PreTitle = styled.strong`
  font-size: 1.4rem;
  text-shadow: 0 .2rem .2rem rgba(0,0,0,.2);
`;

export const SubTitle = styled.h2`

`;

export const Category = styled.h3`
  margin: .5rem;
  padding: 1rem;
`;

export const Instructions = styled.p`
  display: block;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 1rem;
  text-align: center;
`;

export const ActionBar = styled.nav`
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: ${props => props.align ? props.align : 'flex-start'};
  margin: 1rem auto;
  padding: .4rem;
  width: 90%;
`;

export const ActionButton = styled.a`
  background-color: ${props => props.disabled ? $accentDark : $accent};
  border-radius: 1.5rem;
  border-bottom: .25rem solid ${$accentDark};
  border-top: .0625rem solid ${$accentLight};
  box-shadow: ${props => props.disabled ? '0 .1rem .2rem .2rem rgba(0,0,0,.2)' : '0 .4rem .6rem .4rem rgba(0,0,0,.2)'};
  color: ${$light};
  cursor: pointer;
  display: ${props => props.inline ? 'inline-block' : 'block'};
  font-size: 1.5rem;
  font-weight: 700;
  margin: 1rem;
  min-width: 8rem;
  padding: .65rem 1rem;
  position: relative;
  text-align: center;
  top: ${props => props.disabled ? '.25rem' : 'auto'};
  text-decoration: none;
  text-shadow: -.125rem -.125rem .125rem ${$accentDark},
               -.125rem .125rem .125rem ${$accentDark},
               .125rem -.125rem .125rem ${$accentDark},
               .125rem .125rem .125rem ${$accentDark};
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);

  &:hover {
    top: .25rem;
    box-shadow: 0 .1rem .2rem .2rem rgba(0,0,0,.2);
  }

  @media only screen and (min-width: 498px) {
    margin: 1rem ${props => props.inline ? '1rem' : 'auto'};
    max-width: 480px;
  }
`;

const ablTemplate = (type, property) => {
  switch(property) {
    case 'bc':
      return type === 'action' ? $accent : $blue;
    case 'bt':
      return type === 'action' ? $accentLight : $blueLight;
    case 'bb':
    case 'ts':
      return type === 'action' ? $accentDark : $blueDark;
      case 'c':
        return $light;
    default:
      return $accent;
  }
};

export const ActionButtonLink = styled(Link)`
  background-color: ${props=> props.type ? ablTemplate(props.type, 'bc') : $accent};
  border-radius: 1.5rem;
  border-bottom: .25rem solid ${props=> props.type ? ablTemplate(props.type, 'bb') : $accentDark};
  border-top: .0625rem solid ${props=> props.type ? ablTemplate(props.type, 'bt') : $accentLight};
  box-shadow: 0 .4rem .6rem .4rem rgba(0,0,0,.2);
  color: ${props => props.type ? ablTemplate(props.type, 'c') : $light};
  cursor: pointer;
  display: ${props => props.inline ? 'inline-block' : 'block'};
  font-size: 1.5rem;
  font-weight: 700;
  margin: 1rem;
  min-width: 8rem;
  padding: .65rem;
  text-align: center;
  text-decoration: none;
  text-shadow: -.125rem -.125rem .125rem ${props=> props.type ? ablTemplate(props.type, 'bb') : $accentDark},
               -.125rem .125rem .125rem ${props=> props.type ? ablTemplate(props.type, 'bb') : $accentDark},
               .125rem -.125rem .125rem ${props=> props.type ? ablTemplate(props.type, 'bb') : $accentDark},
               .125rem .125rem .125rem ${props=> props.type ? ablTemplate(props.type, 'bb') : $accentDark};

  &:hover {
    position: relative;
    top: .25rem;
    box-shadow: 0 .1rem .2rem .2rem rgba(0,0,0,.2);
  }

  @media only screen and (min-width: 498px) {
    margin: 1rem ${props => props.inline ? '1rem' : 'auto'};
    max-width: 480px;
  }
`;

export const Pane = styled.div`
  background-color: #fff;
  color: ${$dark};
`;

export const CardContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const ChoiceContainer = styled.nav`
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
`;

export const Card = styled.div`
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: 0 .4rem .8rem .4rem rgba(0,0,0,.2);
  color: #101;
  font-size: 1.2rem;
  line-height: 1.6rem;
  margin: .6rem;
  padding: .6rem;

  @media only screen and (min-width: 498px) {
    margin: 1rem auto;
    max-width: 480px;
  }
`;

export const Choice = styled.button`
  background-color: #fff;
  border: 0;
  border-radius: .5rem;
  box-shadow: 0 .2rem .2rem .2rem rgba(0,0,0,.2);
  display: block;
  font-size: 1.4rem;
  margin .4rem auto;
  max-width: 90%;
  outline: 0;
  padding: .6rem;
  text-weight: bold;
  width: 100%;

  &:hover {
    background-color: ${$light};
  }

  @media only screen and (min-width: 498px) {
    margin: 1rem auto;
    max-width: 480px;
  }
`;

export const Question = styled.p`
  display: block;
  text-align: center;
  padding: .4rem;

  @media only screen and (min-width: 498px) {
    margin: 1rem auto;
    max-width: 480px;
  }
`;

export const ProgressContainer = styled.div`
  height: 4rem;
  width: 100%;

  @media only screen and (min-width: 498px) {
    margin: 1rem auto;
    max-width: 480px;
  }
`;

export const ProgressBar = styled.div`
  background-color: ${$red};
  border-radius: 1rem;
  height: 2rem;
  width: ${props => props.progress}%;
`;

export const ProgressTrough = styled.div`
  background-color: ${$dark};
  border-radius: 1.4rem;
  padding: .4rem;
  margin: 0 1rem;
  z-index: 4;
`;

export const ProgressLabel = styled.div`
  background-color: ${$light};
  border-radius: 1rem;
  color: ${$dark};
  display: block;
  margin: 1rem auto;
  padding: .5rem;
  text-align: center;
  text-weight: bold;
  width: 4.6rem;
  z-index: 6;
`;

export const QuestionList = styled.ul`

`;

export const QuestionContainer = styled.li`
  align-content: flex-start;
  align-items: flex-start;
  background: linear-gradient(rgba(0,0,0,.4) 40%, rgba(0,0,0,0));
  border-radius: 2rem 2rem 0 0;
  display: flex;
  flex-direction: row;
  margin: 1rem;
  padding: .8rem;
  // width: 100%;
`;

export const IconContainer = styled.div`
  align-self: flex-start;
  background-color: ${$light};
  border-radius: 50%;
  box-shadow: 0 .2rem .2rem .2rem rgba(0,0,0,.2);
  color: ${props => props.correct ? $accentDark : $redDark};
  flex-shrink: 0;
  height: 2rem;
  padding: .4rem;
  text-align: center;
  width: 2rem;
`;

export const AnswersTitle = styled.h2`
  padding: 1rem;
  text-align: center;
`;

export const Score = styled.strong`
  background-color: ${$light};
  border-radius: 1rem;
  color: ${$dark};
  display: block;
  font-size: 2.6rem;
  margin: 1rem auto;
  padding: .5rem;
  text-align: center;
  text-weight: bold;
  // width: 4.6rem;
`;



