import React from 'react';
// Fontawesome Icon Library
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// Styled Components
import { QuestionContainer, Question, IconContainer } from '../../styled';

const QuestionRow = (props) => (
  <QuestionContainer>
    <IconContainer correct={props.answer === props.correctAnswer}>
      <FontAwesomeIcon icon={props.answer === props.correctAnswer ? 'plus' : 'minus'} />
    </IconContainer>
    <Question>{props.question}</Question>
  </QuestionContainer>
);

export default QuestionRow;



