import React, { Component } from 'react';
// Redux connect to acces state
import { connect } from 'react-redux';
// Import actions
import { questionsFetchData } from '../../redux/actions/questions';
import { quizSetType, quizSetDifficulty } from '../../redux/actions/quiz';
// Components
import Header from '../../components/Header';
import Message from '../../components/Message';
// Styled Components
import { MainContainer, ActionButtonLink, ActionBar, ActionButton, MainNavContainer, BlurbContainer, Title, PreTitle, Instructions } from '../../styled';

class HomeView extends Component {
  state = { difficulty: null, type: null }

  componentDidMount() {
    this.setState({ difficulty: this.props.difficulty, type: this.props.type }, () => {
      this.props.fetchData(`https://opentdb.com/api.php?amount=10&difficulty=${this.props.difficulty}&type=${this.props.type}`);
    });
  }

  componentDidUpdate() {
    const { difficulty, type } = this.props;
    if (this.state.difficulty !== difficulty || this.state.type !== type) {
      this.setState({ difficulty, type }, () => {
        this.props.fetchData(`https://opentdb.com/api.php?amount=10&difficulty=${this.props.difficulty}&type=${this.props.type}`);
      });
    }
  }

  // Handle the start quiz button
  handleStart = (e) => !this.props.fetchSuccess && e.preventDefault();
  // Handle the type set buttons
  setType = (t) => {
    this.props.setType(t);
    // this.props.fetchData(`https://opentdb.com/api.php?amount=10&difficult=${this.props.difficulty}&type=${this.props.type}`);
  }

  render() {
    console.log(this.props.type)
    return (
      <MainContainer className='app-container'>
        <Header>
          <PreTitle>Welcome To</PreTitle>
          <Title>The Trivia Challenge</Title>
        </Header>
        <BlurbContainer>
          <Instructions>
            You will be presented with 10 True or False trivia questions.
          </Instructions>
          <Instructions>
            Can you score 100%?
          </Instructions>
        </BlurbContainer>
        <ActionBar align='center'>
          <ActionButton
            inline={true} 
            disabled={!this.props.fetchSuccess || this.props.type === 'boolean'}
            onClick={()=>this.setType('boolean')}>
              True/False
          </ActionButton>
          <ActionButton
            inline={true}
            disabled={!this.props.fetchSuccess || this.props.type === 'multiple'}
            onClick={()=>this.setType('multiple')}>
              Multiple Choice
          </ActionButton>
        </ActionBar>
        <MainNavContainer>
          <ActionButtonLink to='/quiz' disabled={!this.props.fetchSuccess} onClick={this.handleStart}>Start</ActionButtonLink>

          <div>
            {this.props.awaitingData && (<Message type='status'>Fetch Next Quiz</Message>)}
            {this.props.fetchError && (<Message type='error'>Error fetching data, please reload the app</Message>)}
          </div>
        </MainNavContainer>
      </MainContainer>
    );
  }
}

const mapStateToProps = (state) => {
    return {
        questions: state.questions,
        fetchError: state.questionsFetchError,
        fetchSuccess: state.questionsFetchSuccess,
        awaitingData: state.questionsAwaitingData,
        started: state.quizState,
        score: state.quizScore,
        type: state.quizType,
        difficulty: state.quizDifficulty
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        fetchData: (url) => dispatch(questionsFetchData(url)),
        setType: (t) => dispatch(quizSetType(t)),
        setDifficulty: (d) => dispatch(quizSetDifficulty(d)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeView);



