import React, { Component } from 'react';
// Route to manage navigation routes for views
import { Route, Switch } from 'react-router-dom';
// Styled Components
import styled from 'styled-components';
// Import library manager for fontawesome
import { library } from '@fortawesome/fontawesome-svg-core'
// Import specific icons for the app
import { faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';
// page Views
import { Home, Quiz, Results } from './views';
// Assets
import './css/main.css';

const AppContainer = styled.div`
  height: 100%;
  margin: 0 auto;
  max-width: 960px;
`;

// Add these icons to the library for later
library.add(faMinus, faPlus);

class App extends Component {
  state = { toggle: true };
  toggleToggle = () => this.setState({ toggle: !this.state.toggle });
  render() {
    return (
      <AppContainer>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/quiz' component={Quiz} />
          <Route path='/results' component={Results} />
          <Route component={Home} />
        </Switch>
    </AppContainer>
    );
  }
}

export default App;



