/**
 * Quiz State Actions
 *
 *
 */

// Start quiz action
export function quizStart () {
  return [
    {
      type: 'QUIZ_START',
      started: true
    },
    {
      type: 'QUIZ_CLEAR_ANSWERS',

    }
  ]
}

// End quiz action
export function quizEnd () {
  return {
    type: 'QUIZ_END',
    started: false
  }
}

// Set quiz score action
export function quizScore (score) {
  return {
    type: 'QUIZ_SET_SCORE',
    score
  }
}

// Set the answers
export function quizAddAnswer (answer) {
  return {
    type: 'QUIZ_ADD_ANSWER',
    answer
  }
}

// Increment score from score provided action
export function quizIncrementScore () {
  return {
    type: 'QUIZ_INC_SCORE'
  };
}

// Decrement score from score provided action
export function quizDecrementScore () {
  return {
    type: 'QUIZ_DEC_SCORE'
  }
}

// Set the type of the quiz
export function quizSetType (setType) {
  return {
    type: 'QUIZ_SET_TYPE',
    gameType: setType
  }
}

// Set the difficulty of the quiz
export function quizSetDifficulty (setDiff) {
  return {
    type: 'QUIZ_SET_DIFFICULTY',
    difficulty: setDiff
  }
}



