import React from 'react';
// Styled Components
import { CardContainer, Card, Category, ChoiceContainer, Choice, Question } from '../../styled';

/**
 * Shuffle array --  FisherYates shuffle
 * Thanks to: https://bost.ocks.org/mike/shuffle
 * @method shuffle
 * @param  {array} array The array to shuffle
 * @return {array}       The array, but shuffled
 */
const shuffle = (array) => {
  var m = array.length, t, i;

  // While there remain elements to shuffle
  while (m) {

    // Pick a remaining element
    i = Math.floor(Math.random() * m--);

    // And swap it with the current element.
    t = array[m];
    array[m] = array[i];
    array[i] = t;
  }

  return array;
}

/**
 * Makes options UI based on the type and options with CB action
 * @method makeOptions
 * @param  {string}         type          The type [boolean | multiple]
 * @param  {array->String}  answer        The answer, an array of strings
 * @param  {array->Strings} wrong_answers Wrong Answers, an array of strings
 * @param  {Function}       cb            The function to call on user choice
 * @return {[type]}                       an array of JSX elements
 */
const makeOptions = (type, answer, wrong_answers, cb) => {
  // returned options holder
  let options = [];
  // construct an actionable element from the value as a template
  const elems = (val, i) => (<Choice key={val[i]+i.toString()} onClick={cb}>{val}</Choice>);

  // If the typoe of the question is boolean, the values are known
  if (type === 'boolean') {
    // create an array of the values and map with the template creator
    options = ['True', 'False'].map(elems);

  // it's another type, assumed to be "multiple"
  } else {
    // Add the true answer to the wrong answers array
    const answers = wrong_answers.concat(answer);
    // iterate over the answers and assing to options
    options = answers.map(elems);
  }

  // Shuffle the items if they aren't boolean options
  if (type !== 'boolean') options = shuffle(options);

  return options;
}

const CardElement = (props) => (
  <CardContainer className='card-container'>
    <Category>{props.category}</Category>
    <Card className='card'>
      <Question>{props.question}</Question>
    </Card>
    <ChoiceContainer>
      {makeOptions(props.type, props.correct_answer, props.incorrect_answers, props.answerQuestion)}
    </ChoiceContainer>
  </CardContainer>
);

export default CardElement;



