export function questions (state = [], action) {
  switch (action.type) {
    case 'QUESTIONS_FETCH_DATA_SUCCESS':
        return action.questions.results;
    case 'QUESTION_ANSWERED':
      return state.map((q) => {
        // If the questions match, return the question
        if (q.question === action.question.question) return action.question.question;
        // Return the question because it's not the correct match
        return q;
      });
    case 'QUESTIONS_ANSWERED':
      //TODO implement multiple questions via array of questions
      return state;
    default:
      return state;
  }
}

export function questionsAwaitingData (state = false, action) {
  switch (action.type) {
    case 'QUESTIONS_AWAITING_DATA':
      return action.awaitingData;

    default:
      return state;
  }
}

export function questionsFetchSuccess (state = false, action) {
    switch (action.type) {
        case 'QUESTIONS_FETCH_DATA_SUCCESS':
            return true;
        default:
            return state;
    }
}

export function questionsFetchError (state = null, action) {
  switch (action.type) {
    case 'QUESTIONS_FETCH_DATA_SUCCESS':
      return false;
    case 'QUESTIONS_FETCH_ERROR':
      return action.fetchError;

    default:
      return state;
  }
}



