import React from 'react';
// Redux connect to acces state
import { connect } from 'react-redux';
// Redirect from react-router
import { Redirect } from 'react-router-dom';
// Import actions
import { quizEnd, quizScore } from '../../redux/actions/quiz';
// Import Components
import Question from '../../components/Question';
// Styled Components
import { MainContainer, AnswersTitle, QuestionList, Score, ActionButtonLink } from '../../styled';

const restartQuiz = (props) => {
  props.quizClearAnswers()
  props.quizEnd();
  // props.history.push('/');
}

const ResultsView = (props) => {
  return (
    <MainContainer className='app-container'>
      <AnswersTitle>You Scored</AnswersTitle>
      <Score>{props.score} / {props.questions.length}</Score>
      {props.questions.length <= 0 && (<Redirect to='/' />)}
      <QuestionList>
        {props.questions.map((question, i) => (
          <Question key={`answer${i}`}
            question={question.question}
            answer={props.answers[i]}
            correctAnswer={question.correct_answer}
          />
        ))}
      </QuestionList>
      <nav>
        <ActionButtonLink onClick={() => { restartQuiz(props); }} to='/'>Start Over</ActionButtonLink>
      </nav>
    </MainContainer>
  )
}

const mapStateToProps = (state) => {
  return {
    answers: state.quizAnswers,
    questions: state.questions,
    fetchSuccess: state.questionsFetchSuccess,
    started: state.quizState,
    score: state.quizScore
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    quizEnd: () => dispatch(quizEnd()),
    quizScore: (s) =>dispatch(quizScore(s))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ResultsView);



