import React, { Component } from 'react';
// Redux connect to acces state
import { connect } from 'react-redux';
// Redirect from react-router
import { Redirect } from 'react-router-dom';
// Import actions
import { quizStart, quizEnd, quizIncrementScore, quizAddAnswer } from '../../redux/actions/quiz';
// Components
import Card from '../../components/Card';
import Progress from '../../components/Progress'
// Styled Components
import { MainContainer } from '../../styled';

class QuizView extends Component {
  constructor(props) {
    super(props);
    // track the length of the questions array
    let len = null;
    // verify that questions were successfully fetched
    if (props.fetchSuccess) {
      // pass the length, but if it's 0, leave as null
      len = props.questions.length > 0 ? props.questions.length : null;
    }
    // Set the local state to track this quiz session
    this.state = { index: 0, length: len };
  }

  componentDidMount() {
    // If somehow they got to this point and aren't redirected away because of no questions,
    // start the quiz
    if (!this.props.started) this.props.quizStart();
  }

  answerQuestion = (e) => {
    e.preventDefault();
    // Get the correct Answer for the question
    const correctAnswer = this.props.questions[this.state.index].correct_answer;
    // Get the chosen answer from the user's event
    const chosen = e.target.innerHTML;
    // Add the answer to the store
    this.props.quizAddAnswer(chosen);
    // Increment the score if it's correct
    if (correctAnswer === chosen) this.props.incrementScore();
    // set the local state to track the index of questions
    this.setState({ index: this.state.index + 1 }, () => {
      // if the index is the length or greater
      if (this.state.index >= this.state.length) {
        // move to results page
        this.props.history.push('/results');
      }
    });
  }

  isMoreCards = () => (this.state.index + 1) <= this.state.length;

  render() {
    return (
      <MainContainer className='app-container'>
        {!this.props.fetchSuccess && (<Redirect to='/' />)}
        {this.props.fetchSuccess && this.isMoreCards() && (
          <Card {...this.props.questions[this.state.index]} answerQuestion={this.answerQuestion} />
        )}
        <Progress
          progress={this.state.index + 1}
          total={this.state.length}
        >{this.state.index + 1} / {this.state.length}</Progress>
      </MainContainer>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    answers: state.quizAnswers,
    questions: state.questions,
    fetchSuccess: state.questionsFetchSuccess,
    started: state.quizState,
    score: state.quizScore
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    quizStart: () => dispatch(quizStart()),
    quizEnd: () => dispatch(quizEnd()),
    incrementScore: () => dispatch(quizIncrementScore()),
    quizAddAnswer: (a) => dispatch(quizAddAnswer(a))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(QuizView);



