// Decode HTML Unicode characters
// import decode from 'unescape';
import decode from 'ent/decode';

/**
 * Redux questions actions
 *
 *
 */

export function questionAnswered (question) {
  return {
    type: 'QUESTION_ANSWERED',
    question
  }
}

// Questions Fetch Success Action
export function questionsFetchDataSuccess (questions) {
  return {
    type: 'QUESTIONS_FETCH_DATA_SUCCESS',
    questions
  }
}


// Questions Fetch Error Action
export function questionsFetchError (bool) {
  return {
    type: 'QUESTIONS_FETCH_ERROR',
    fetchError: bool
  };
}


// Questions fetch timeout to dispatch Error action
export function questionFetchTimeout() {
  // return the timeout function
  return (dispatch) => {
    setTimeout(() => {
      // Dispatch the errored event after a timeout period
      dispatch(questionsFetchError(true));
    }, 5000);
  };
}


// Questions Awaiting Data action
export function questionsAwaitingData (bool) {
  return {
    type: 'QUESTIONS_AWAITING_DATA',
    awaitingData: bool
  };
}


// Fetch Data action Dispatcher, using Thunk.
export function questionsFetchData (url) {
  return (dispatch) => {
    // Set to true, so that we know we are waiting for data
    dispatch(questionsAwaitingData(true));
    // actually get the data
    fetch(url)
      .then((response) => {
        // Was there an error with the response?
        if (!response.ok) {
          // Make it be known
          throw Error(response.statusText);
        }
        // set the awaiting data to false, meaning we aren't anymore.
        dispatch(questionsAwaitingData(false));
        // Our response is moving on to the next steps
        return response;
      })
      // process the json
      .then((response) => response.json())
      // map over questions and answers and correct unicode characters
      .then((response) => {
        response.results = response.results.map((q) => {
          q.question = decode(q.question, 'all');
          q.correct_answer = decode(q.correct_answer, 'all');
          q.incorrect_answers.map(ia => decode(ia));
          return q;
        });
        return response;
      })
      // Take in the questions and set them
      .then((questions) => dispatch(questionsFetchDataSuccess(questions)))
      .catch(() => dispatch(questionsFetchError))
  };
}



