export function quizIndex (state = 0, action) {
  switch (action.type) {
    case 'INC_INDEX':
      return state + action.ammount;
    case 'DEC_INDEX':
      return state - action.ammount;

    default:
      return state;
  }
}

export function quizState (state = false, action) {
  switch (action.type) {
    case 'QUIZ_END':
    case 'QUIZ_START':
      return action.started;

    default:
      return state;
  }
}

export function quizAnswers (state = [], action) {
  switch(action.type) {
    case 'QUIZ_ADD_ANSWER':
      return state.concat(action.answer)
    case 'QUIZ_CLEAR_ANSWERS':

    default:
      return state;
  }
}

export function quizScore (state = 0, action) {
  switch (action.type) {
    case 'QUIZ_DEC_SCORE':
      return state > 0 ? state - 1 : 0;
    case 'QUIZ_INC_SCORE':
      return state + 1;
    case 'QUIZ_SET_SCORE':
      return action.score;
    case 'QUIZ_CLEAR_ANSWERS':
      return 0;

    default:
      return state;
  }
}

export function quizType (state = 'boolean', action) {
  switch (action.type) {
    case 'QUIZ_SET_TYPE':
      return action.gameType;

    default:
      return state;
  }
}

export function quizDifficulty (state = 'hard', action) {
  switch (action.type) {
    case 'QUIZ_SET_DIFFICULTY':
      return action.difficulty;

    default:
      return state;
  }
}



