



// Redux's createStore and apply middleware to export our store
import { createStore, applyMiddleware } from 'redux';
// reduxMulti allows multiple functions and arrays
import reduxMulti from 'redux-multi';
// batchedSubscribe allows listeners to only be called once on batch subscribes
import { batchedSubscribe } from 'redux-batched-subscribe';
// Thunk to have async in redux
import thunk from 'redux-thunk';

// The reducers to create the store from
import rootReducer from '../reducers';

export default function configureStore(initialState) {
  return createStore(
    rootReducer,
    initialState,
    applyMiddleware(thunk, reduxMulti),
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
  );
}



